.rain {
  position: absolute;
  left: 0;
  width: 95%;
  height: 100%;
  z-index: 2;
}

.rain.front-row {
  margin-top: 0px;
  opacity: 0.8;
}

.rain.back-row {
  display: none;
  z-index: 1;
  opacity: 0.5;
  margin-top: 0px;
}

body.back-row-toggle .rain.back-row {
  display: block;
}

.drop {
  position: absolute;
  bottom: 100%;
  width: 18px;
  height: 120px;
  pointer-events: none;
  animation: drop 1s linear infinite;
}

@keyframes drop {
  0%   { transform: translateY(0); }
  100% { transform: translateY(var(--fall-distance)); }
}

.stem {
  width: 1px;
  height: 60%;
  margin-left: 7px;
  background: #808080;
  animation: stem 1s linear infinite;
}

@keyframes stem {
  0% {
    opacity: 0.5;
  }
  65% {
    opacity: 0.5;
  }
  75% {
    opacity: 0.5;
  }
  100% {
    opacity: 0.25;
  }
}

.splat {
  width: 15px;
  height: 10px;
  border-top: 2px dotted rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  opacity: 0.8;
  transform: scale(0);
  animation: splat 1s linear infinite;
  display: none;
}

body.splat-toggle .splat {
  display: block;
}

@keyframes splat {
  0% {
    opacity: 0.8;
    transform: scale(0);
  }
  80% {
    opacity: 0.8;
    transform: scale(0);
  }
  90% {
    opacity: 0.5;
    transform: scale(1);
  }
  100% {
    opacity: 0.2;
    transform: scale(1.5);
  }
}

.content {
  z-index: 3;
}

.splat-toggle {
  top: 20px;
}

.back-row-toggle {
  top: 90px;
  padding-top: 14px;
}
